<template>
  <article>
    <section class="mb-3">
      <div class="d-flex">
        <div class="mr-5">
          <button class="btn btn-primary" @click.prevent="genderSelect = 'women',getCategory()"></button>
        </div>
        <div v-if="genderSelect" class="flex-grow-1 d-flex mb-3">
          <button class="btn btn-primary mr-2" @click="openBigCategoryTemp(false)"></button>
          <button class="btn btn-primary ml-auto" @click="saveAllModify"></button>
        </div>
      </div>
      <div v-if="bigCategoryShow === -1">
        
        <input type="text" v-model="bigCategoryTemp">
        <button class="btn btn-primary ml-2" @click="addBigCategory()"></button>
        <button class="btn btn-primary ml-2" @click="closeBigCategoryTemp"></button>
      </div>
    </section>
    <section
      class="list-group border p-3"
      v-for="(bigCategory,bigIndex) in categoryData"
      :key="bigIndex"
    >
      <div class="mb-3">
        <span class="d-inline-block mb-3">{{bigCategory.bigName}}</span>
        <button
          class="ml-2 d-inline-block btn btn-primary"
          @click="openBigCategoryTemp(bigIndex)"
        ></button>
        <div v-if="bigCategoryShow === bigIndex">
          
          <input type="text" v-model="bigCategoryTemp">
          <button class="btn btn-primary ml-2" @click="modifyBigCategory(bigIndex)"></button>
          <button class="btn btn-primary ml-2" @click="closeBigCategoryTemp"></button>
        </div>
      </div>
      <draggable
        v-model="categoryData[bigIndex]['category']"
        group="people"
        @start="drag=true"
        @end="drag=false"
      >
        <div v-for="(category,index) in categoryData[bigIndex]['category']" :key="index">
          <div
            class="list-group-item list-group-item-action"
            v-on:dblclick="openCategoryTemp(bigIndex,index)"
          >&#8594;{{category.name}}({{category.id}})</div>
          <div
            class="list-group-item list-group-item-action position-relative"
            v-if="categoryShow === categoryData[bigIndex]['category'][index]"
          >
            <div class="mb-3">
              <span>
                
                <input type="text" v-model="categoryTemp.name">
              </span>
              <span class="ml-2">
                ID
                <input type="text" v-model="categoryTemp.id">
              </span>
            </div>
            <div>
              <button
                class="close_btn btn-primary btn border flex-center d-inline-flex py-1 px-3"
                @click="modifyCategory(bigIndex,index)"
              ></button>
              <button
                class="close_btn btn-primary btn border flex-center d-inline-flex py-1 px-3 ml-2"
                @click="deleteCategory(bigIndex,index)"
              ></button>
              <button
                class="close_btn btn-primary btn border flex-center d-inline-flex py-1 px-3 ml-2"
                @click="closeCategoryTemp"
              ></button>
            </div>
          </div>
        </div>
      </draggable>
      <div
        class="list-group-item list-group-item-action"
        v-on:dblclick="openCategoryTemp(bigIndex,null,true)"
      >&#8594;</div>
      <div class="list-group-item list-group-item-action" v-if="categoryShow === bigIndex">
        <div class="mb-3">
          <span>
            
            <input type="text" v-model="categoryTemp.name">
          </span>
          <span class="ml-2">
            ID
            <input type="text" v-model="categoryTemp.id">
          </span>
        </div>
        <div>
          <button
            class="close_btn btn-primary btn border flex-center d-inline-flex py-1 px-3"
            @click="modifyCategory(bigIndex)"
          ></button>
          <button
            class="close_btn btn-primary btn border flex-center d-inline-flex py-1 px-3 ml-2"
            @click="closeCategoryTemp"
          ></button>
        </div>
      </div>
    </section>
  </article>
</template>
<script>
import draggable from "vuedraggable";
import AxiosClass from "@/config/axiosClass.js";
import $ from "jquery";
export default {
  components: {
    draggable
  },
  data() {
    return {
      bigCategoryTemp: "",
      categoryShow: {},
      genderSelect: "",
      categoryData: [],
      categoryTemp: {
        name: "",
        id: ""
      },
      isNewCategory: false,
      bigCategoryShow: ""
    };
  },
  methods: {
    addBigCategory() {
      const addBigCategory = new AxiosClass(
        `admin/category_gender/${this.genderSelect}`,
        "post",
        {bigName:this.bigCategoryTemp},
        true
      );
      addBigCategory.doAxios(
        this,
        () => {
          this.$emit("push-alert", "");
        },
        () => {
          this.$emit("push-alert", "", true);
        }
      );
    },
    saveAllModify() {
      const saveAllModify = new AxiosClass(
        `admin/category_gender/${this.genderSelect}`,
        "put",
        this.categoryData,
        true
      );
      saveAllModify.doAxios(
        this,
        () => {
          this.$emit("push-alert", "");
        },
        (res) => {
          this.$emit("push-alert", ""+res, true);
        }
      );
    },
    openCategoryTemp(bigIndex, index, isNew = false) {
      if (isNew) {
        this.isNewCategory = true;
        this.categoryTemp.id = "";
        this.categoryTemp.name = "";
        this.categoryShow = bigIndex;
      } else {
        this.isNewCategory = false;
        this.categoryShow = this.categoryData[bigIndex]["category"][index];
        this.categoryTemp.id = this.categoryData[bigIndex]["category"][
          index
        ].id;
        this.categoryTemp.name = this.categoryData[bigIndex]["category"][
          index
        ].name;
      }
    },
    closeCategoryTemp() {
      this.categoryTemp.id = "";
      this.categoryTemp.name = "";
      this.categoryShow = {};
      this.isNewCategory = false;
    },
    getCategory() {
      const getCategory = new AxiosClass(
        `admin/category_gender/${this.genderSelect}`,
        "get",
        null,
        true
      );
      getCategory.doAxios(
        this,
        res => {
          this.categoryData = res.data.data;
          this.$emit("push-alert", "");
        },
        () => {
          this.$emit("push-alert", "", true);
        }
      );
    },
    modifyCategory(bigIndex, index = false) {
      if (!this.isHasSameID(bigIndex)) {
        const tempCopy = Object.assign({}, this.categoryTemp);
        if (index !== false) {
          this.categoryData[bigIndex]["category"][index] = tempCopy;
        } else {
          this.categoryData[bigIndex]["category"].push(tempCopy);
        }
        this.closeCategoryTemp();
      }
    },
    deleteCategory(bigIndex, index) {
      this.categoryData[bigIndex]["category"].splice(index, 1);
    },
    isHasSameID(bigIndex) {
      for (const item of this.categoryData[bigIndex]["category"]) {
        if (item.id === this.categoryTemp.id) {
          this.$emit("push-alert", "ID", true);
          return true;
        }
      }
      return false;
    },
    modifyBigCategory(bigIndex) {
      this.categoryData[bigIndex]["bigName"] = this.bigCategoryTemp;
      this.bigCategoryShow = 999;
    },
    openBigCategoryTemp(bigIndex = false) {
      if (bigIndex !== false) {
        this.bigCategoryShow = bigIndex;
        this.bigCategoryTemp = this.categoryData[bigIndex]["bigName"];
      } else {
        this.bigCategoryShow = -1;
        this.bigCategoryTemp = "";
      }
    },
    closeBigCategoryTemp() {
      this.bigCategoryTemp = "";
      this.bigCategoryShow = 999;
    }
  },
  created() {}
};
</script>
<style lang="scss" scoped>
.close_btn:hover {
  cursor: pointer;
}
</style>
